@import url(https://fonts.googleapis.com/css2?family=Karla:wght@100&family=Montserrat:wght@300&family=Roboto:ital,wght@0,100;0,100;1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200&family=Montserrat:wght@300&family=Roboto:ital,wght@0,700;0,700;1,500&display=swap);
* {
  box-sizing: border-box;
}

body {
  /* font-family: sans-serif; */
  font-family: "Karla", sans-serif;
  margin: 0;
  margin-bottom:8rem;
  background-color: hsl(240, 1%, 13%);
}

.centered {
  margin: 3rem auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;

  font-family: sans-serif;
  font-weight: 6px;
  font-size: 12px;
}

.focused {
  font-size: 3rem;
  font-weight: bold;
  color: white;
}

.btn {
  text-decoration: none;
  padding: 1rem;
  cursor: pointer;
  margin: 1rem 0;

  color: rgba(255, 255, 255, 0.727);
  background-color: rgba(66, 51, 104, 0.316);
  box-shadow: 2px 3px 4px rgba(0, 0, 0, 0);
  border-radius: 0.3rem;
  transition: all 0.05s;

 
}
@media (max-width: 600px) {
  .btn {
    width:100%;
  }
}
  
 

.btn:hover,
.btn:active {
  color: rgb(0, 132, 255);
  background-color: rgba(57, 70, 168, 0.35);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0);
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.btn--flat {
  cursor: pointer;
  font-family: sans-serif;
  font-weight: 12px;
  text-decoration: none;
  border: none;
  padding: 0.75rem 1.5rem;
  /* color: teal; */
  border-radius: 7px;
  color: rgb(255, 255, 255);
  background-color: rgb(0, 86, 177);
  box-shadow: 2px 3px 10px rgba(26, 26, 26, 0);
}

.btn--flat:hover {
  background-color: rgb(0, 100, 208);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.912);
}
.btn--flat:active {
  background-color: rgb(0, 67, 167);
  box-shadow: 2px 3px 90px rgba(0, 0, 0, 0);
  /* border-color: #11acac; */
}

ul .bottonLink {
  text-decoration: none;
}

input,
textarea,
button,
div,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.Layout_main__3y929 {
  margin: 6rem auto;
  width: 90%;
  max-width: 40rem;
}
.MainNavigation_header__zx6DM {
  width: 100%;
  height: 4rem;
  display: flex;
  padding: 0 5%;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(260, 5%, 13.6%);
  box-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.427);

  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}

.MainNavigation_logo__3xeQh {
  font-size: 1.7rem;
  color: rgb(255, 255, 255);
  text-decoration: none;
  cursor: pointer;
}

.MainNavigation_nav__3a6tk ul {
  list-style: none;
  display: flex;
  padding: 0;
  margin-left: 0.5rem;
}

.MainNavigation_nav__3a6tk li {
  margin: 0 0.1rem;
  font-size: 0.8rem;
  font-weight: 8px;
}

.MainNavigation_nav__3a6tk a {
  text-decoration: none;
  color: #9c91ff;
  cursor: pointer;
  font-family: sans-serif;
  padding: 0.5rem 0.8rem;
  border: none;
  border-radius: 0.4rem;
}

.MainNavigation_nav__3a6tk a:hover,
.MainNavigation_nav__3a6tk a:active,
.MainNavigation_nav__3a6tk a.MainNavigation_active__2QphT {
  color: rgb(243, 245, 255);
  background-color: rgba(122, 93, 177, 0.254);
}

.BackToTop_container__3BSNf {
  display: none;
}

@media (max-width: 700px) {
  .BackToTop_container__3BSNf {
    cursor: pointer;
    position: fixed;
    padding: 0rem 0.1rem 0.3rem 0.1rem;
    bottom: 0rem;
    font-size: 1.4rem;
    left: 0;
    /* right:0rem; */
    height: 5.3rem;
    width: 100%;
    color: hsl(0, 0%, 77%);
    border: none;

    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 9;

    background: hsl(240, 9%, 15%);
    border-top: 0.1rem solid hsl(255, 6%, 15%);
  }

  .BackToTop_container__3BSNf li {
    list-style: none;
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    /* border: 0.1rem solid lightgrey; */
  }
  .BackToTop_container__3BSNf li div a {
    margin: 0rem 0 0.2rem 0;
    font-size: 1.7rem;
    padding: 0.45rem 1.6rem;
    border-radius: 2rem;
    transition: all 0.07s;

    display: flex;
    align-items: center;

    color: hsl(0, 0%, 76%);
  }

  .BackToTop_container__3BSNf li div:hover > a,
  .BackToTop_container__3BSNf li div a:active,
  .BackToTop_container__3BSNf li div a.BackToTop_active__3skmo {
    background-color: hsla(225, 100%, 75%, 0.163);
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    color: rgb(220, 231, 255);
    font-weight: bold;
  }

  .BackToTop_container__3BSNf li span {
    font-size: 0.9rem;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1px;
  }
}

